.Intro {
  display: flex;
  height: 77vh;
  margin-top: 4rem;
  width: 90%;

}


/* left side styling */
.i-left {
  display: flex;
  position: relative;
  flex: 1;
  flex-direction: column;
  gap: 2rem;
}
.i-name {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  margin-left: 1rem;
  
}
.i-name > :nth-child(1) {
  color: gold;
  font-weight: bold;
  font-size: 3rem;

 
}
.i-name > :nth-child(2) {
  color: rgb(57, 66, 240);
  font-weight: bold;
  font-size: 3rem;
}
.i-name > :nth-child(3) {
  font-weight: 100;
  font-size: 20px;
  color: var(--gray);
  margin-top: 10px;
  margin-left: 1rem;

}
.i-button {  
  background: gold;
  box-shadow: 0px 20px 24px 3px rgba(21, 21, 21, 0.42);
  border-radius: 34px;
  border: none;
  color: rgb(17, 16, 16);
  font-size: 16px;
  padding: 11px 26px 11px 26px;
  cursor: pointer;
  margin-left: 10rem;
}
.i-icons {
  margin-top: 1rem;
  display: flex;
  gap: 0rem;
}
.i-icons > * {
  transform: scale(0.5);
}
.i-icons > *:hover {
  cursor: pointer;
}
/* right side styling */
.i-right {
  flex: 1;
  position: relative;
}
.i-right > * {
  position: absolute;
  z-index: 1;
}
.i-right > :nth-child(1) {
  transform: scale(0.69);
  left: -15%;
  top: -9%;
}
.i-right > :nth-child(2) {
  top: -4.6rem;
  transform: scale(0.67);
  left: -3rem;
}
.i-right > :nth-child(3) {
  transform: scale(1.4);
  left: 28%;
}
.i-right > :nth-child(4) {
  transform: scale(0.62);
  top: -19%;
  left: -24%;
  border-radius: 50%;
  padding: 0;
}

/* blur */
.blur {
  position: absolute;
  width: 22rem;
  height: 14rem;
  border-radius: 50%;
  background: #edd0ff;
  filter: blur(212px);
  z-index: -9;
  top: -18%;
  left: 56%;
  filter: blur(72px);
}

@media screen and (max-width: 480px) {
  .Intro {
    margin-top: 0;
    flex-direction: column;
    gap: 5rem;
    height: 66rem;
    padding: 0;
  }

  .i-right {
    transform: scale(0.8);
    left: -3rem;
  }
  .i-right .blur {
    display: none;
  }
  .floating-div:nth-of-type(1){
    /* display: none; */
    top: -7rem !important;;
  }
}
